import test from 'ava';
import {
    getApi,
    auth,
    logout,
    responseErrMessage as err
} from '../shortcuts';

test.before('Create feed', async t => {
    // Authorise
    t.context.api = await getApi();
    auth(t.context.api);

    // Delete all existing feeds
    const respFeeds = await t.context.api.get('/feeds');
    t.is(respFeeds.status, 200, err(respFeeds));
    const feeds = respFeeds.data;
    let feedKey;
    for (let i in feeds) {
        feedKey = feeds[i].key;
        const respDelete = await t.context.api.delete(`/feeds/${feedKey}`);
        t.is(respDelete.status, 200, err(respDelete));
    }

    // Create feed
    const feed = { name: 'autotest-feed-default', description: 'default feed generated by api autotest'};
    const resp  = await t.context.api.post('/feeds', feed);
    t.is(resp.status, 201, err(resp));
    t.context.feed = resp.data;
});

test('Add data to feed works', async t => {
    const data = { value: 42 };
    const respData = await t.context.api.post(`/feeds/${t.context.feed.key}/data`, data);
    t.is(respData.status, 200, err(respData));
    t.is(respData.data.value, data.value.toString());
    t.is(respData.data.feed_key, t.context.feed.key);
});
